
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'ConfigDropdownTrigger',
  props: {
    ariaLabel: {
      type: String,
      required: true,
    },
    open: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['onClose', 'onOpen'],
})
